import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { Validators, FormGroup, FormBuilder } from '@angular/forms';
import { Subject } from 'rxjs';
import { AuthUserService } from 'src/app/core/auth-user.service';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { OrderManagerService } from '../order-manager.service';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-add-order',
  templateUrl: './add-order.component.html'
})
export class AddOrUpdateOrderComponent implements OnInit , OnDestroy {
  private ngUnsubcribe = new Subject();
  orderForm: FormGroup;
  logginIn = false;
  error: string;

  @Input()
  order: any;

  @Input()
  operation: 'CREATE' | 'UPDATE' = 'CREATE';
  constructor(
    // private router: Router,
     private formBuilder: FormBuilder,
     private activeModal: NgbActiveModal,
     private orderManager: OrderManagerService,
    // private authManagerService: AuthManagerService,
    // private route: ActivatedRoute,

  ) {
  }
  ngOnDestroy() {
    this.ngUnsubcribe.next();
    this.ngUnsubcribe.complete();
  }

  // user_name: req.body.user_name,
  // user_id: req.body.user_id,
  // status: req.body.status,
  // price: req.body.price,
  // owner_id: req.body.owner_id,
  // product: req.body.product,
  ngOnInit(): void {
    console.log('Order_id: ', this.order);
    this.orderForm = this.formBuilder.group({
      order_id : this.order.order_id,
      status: [this.order.status, Validators.required],
      owner_id: [this.order.owner_id, Validators.required]
    });
  }

  submit(): void {
    if (this.operation == 'CREATE') {
      this.orderManager.createOrder(this.orderForm.value)
        .pipe(takeUntil(this.ngUnsubcribe))
        .subscribe((data: any) => {
          this.activeModal.close(true);
          console.log('data: ', data.vendor);
        });
    } else {
      this.orderManager.updateOrder(this.orderForm.value)
        .pipe(takeUntil(this.ngUnsubcribe))
        .subscribe((data: any) => {
          this.activeModal.close(true);
          console.log('data: ', data.vendor);
        });
    }
  }

  cancel(): void {
    this.activeModal.close(false);
  }
}
