import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subject, from } from 'rxjs';
import { OrderManagerService } from '../order-manager.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { filter, takeUntil } from 'rxjs/operators';
import { AddOrUpdateOrderComponent } from '../add-order/add-order.component';
import { ConfirmDeleteComponent } from '../confirm-delete/confirm-delete.component';
import { AuthUserService } from 'src/app/core/auth-user.service';

@Component({
  selector: 'app-order-list',
  templateUrl: './order-list.component.html'
})
export class OrderListComponent implements OnInit, OnDestroy {
  private ngUnsubcribe = new Subject();

  constructor(public orderManagerService: OrderManagerService, private ModalService: NgbModal, private authUserService: AuthUserService) {
  }

  ngOnInit(): void {
    this.orderManagerService.getOrderList();
  }

  ngOnDestroy() {
    this.ngUnsubcribe.next();
    this.ngUnsubcribe.complete();
  }


  addOrderModal(): void {
    const modalRef = this.ModalService.open(AddOrUpdateOrderComponent, {
      size: 'lg',
      backdrop: 'static'
    });
    const order = {
      _id: '',
      name: undefined,
      status: 'Active',
      seating: undefined,
      owner_id: this.authUserService.user._id,
      created_date: new Date().valueOf(),
      updated_date: new Date().valueOf()
    }
    modalRef.componentInstance.order = order;
    modalRef.componentInstance.operation = 'CREATE';
    from(modalRef.result)
      .pipe(
        filter((data: any) => data),
        takeUntil(this.ngUnsubcribe))
      .subscribe(() => this.orderManagerService.getOrderList());
  }

  deleteOrder(order: any): void {
    const modalRef = this.ModalService.open(ConfirmDeleteComponent, {
      size: 'lg',
      backdrop: 'static'
    });
    modalRef.componentInstance.order = order;
    from(modalRef.result)
      .pipe(
        filter((data: any) => data),
        takeUntil(this.ngUnsubcribe))
      .subscribe(() => this.orderManagerService.getOrderList());
  }

  updateOrder(order: any): void {
    const modalRef = this.ModalService.open(AddOrUpdateOrderComponent, {
      size: 'lg',
      backdrop: 'static'
    });

    order['updated_date']= new Date().valueOf();
    
    modalRef.componentInstance.order = order;
    modalRef.componentInstance.operation = 'UPDATE';
    from(modalRef.result)
      .pipe(
        filter((data: any) => data),
        takeUntil(this.ngUnsubcribe))
      .subscribe(() => this.orderManagerService.getOrderList());
  }
}
