import { Observable, BehaviorSubject } from 'rxjs';
import { OrderService } from '../core/order.service';
import { AuthUserService } from '../core/auth-user.service';
import { SubcategoryService } from '../core/subcategory.service';
import { CategoryService } from '../core/category.service';
import { Injectable } from '@angular/core';
import { TableState } from '../core/table-state';

@Injectable()
export class OrderManagerService {

  public state:TableState = {
    page: 1,
    pageSize: 10,
    searchTerm: '',
    sortColumn: '',
    sortDirection: ''
  }

  private _total$ = new BehaviorSubject<number>(1);
  get total$(): Observable<number> {
    return this._total$.asObservable();
  }

private originalList = [];
  private _orderList$ = new BehaviorSubject<any[]>(null);
  
  get orderList$(): Observable<any[]> {
    return this._orderList$.asObservable();
  }

  private _subcategoryList$ = new BehaviorSubject<any[]>(null);
  get subcategoryList$(): Observable<any[]> {
    return this._subcategoryList$.asObservable();
  }
  
  private _categoryList$ = new BehaviorSubject<any[]>(null);
  get categoryList$(): Observable<any[]> {
    return this._categoryList$.asObservable();
  }

  constructor(private orderService: OrderService,
    private authUserService: AuthUserService,
    private subcategoryService: SubcategoryService,
    private categoryService: CategoryService) {
      this.getCategoryList();
    }



  createOrder(order: any): Observable<any> {
    return this.orderService.createOrder(order);
  }

  updateOrder(order: any): Observable<any> {
    return this.orderService.updateOrder(order);
  }

  deleteOrder(id: string): Observable<any> {
    return this.orderService.deleteOrder(id);
  }

  getOrder(id: string): Observable<any> {
    return this.orderService.getOrder(id);
  }


  getOrderList(): void {
    this.orderService.getOrderList(this.authUserService.user._id)
      .subscribe((data: any[]) => {
        console.log('Got Orders for you: ', data);
        this._orderList$.next(data);
        this.originalList = data;
        this._total$.next(data.length)
      });
  }

  getSubcategoryListForCategory(category_id: string): void {
    this.subcategoryService.getSubcategoryListForCategory(this.authUserService.user._id, category_id)
      .subscribe((data: any[]) => {
        console.log('Got Subcategories for you: ', data);
        this._subcategoryList$.next(data);
      });

  }
  getCategoryList(): void {
    this.categoryService.getCategoryList(this.authUserService.user._id)
      .subscribe((data: any[]) => {
        console.log('Got Subcategories for you: ', data);
        this._categoryList$.next(data);
      });
  }

  searchText(): void {
    if (!this.state.searchTerm) {
      this._orderList$.next(this.originalList);
      this._total$.next(this.originalList.length)
    } else {
      const searchTerm = this.state.searchTerm.toLocaleLowerCase();
      const filteredList = this.originalList.filter((item: any) => item['user_name'] && item['user_name'].toLocaleLowerCase().includes(searchTerm) ||
      item['status'] && item['status'].toLocaleLowerCase().includes(searchTerm) ||
      item['product'] && item['product'].toLocaleLowerCase().includes(searchTerm)
      );
      this._orderList$.next(filteredList);
      this._total$.next(filteredList.length);
    }
  }}
